import React, { PureComponent } from 'react';
import { ValidatorForm } from 'react-material-ui-form-validator';
import ReferenceNumber from './fields/idc1Fields/ReferenceNumber';
import DocumentTypeField from './fields/idc1Fields/DocumentType';
import CompanyTaxNumberField from './fields/idc1Fields/CompanyTaxNumber';
import CompanyBranchField from './fields/idc1Fields/CompanyBranch';
import CompanyNameFieldTH from './fields/idc1Fields/CompanyNameTH';
import CompanyNameFieldEN from './fields/idc1Fields/CompanyNameEN';
import AddressField from './fields/idc1Fields/AddressField';
import AddressSelectors from './fields/idc1Fields/AddressSelectors';
import PostCode from './fields/idc1Fields/PostCode';
import FieldHeader from '../shared/FieldHeader';

const fieldList = [
  'referenceNumber',
  'documentType',
  'companyTaxNumber',
  'companyBranch',
  'companyNameTH',
  'companyNameEN',
  'streetAndNumber',
  'postCode',
];

class ImportDeclarationControl1 extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      referenceNumber: '',
      documentType: '',
      companyTaxNumber: '',
      companyBranch: '',
      companyNameTH: '',
      companyNameEN: '',
      streetAndNumber: '',
      postCode: '',
    };
  }

  componentDidMount() {
    fieldList.forEach(field => {
      if (localStorage.getItem(field) !== null) {
        this.setState({
          [field]: localStorage.getItem(field),
        });
      }
    });
  }

  saveToLocalStorage = data => {
    for (const pair of data.entries()) {
      localStorage.setItem(pair[0], pair[1]);
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    const data = new FormData(e.target);
    this.saveToLocalStorage(data);
    this.props.handlenext();
  };

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  render() {
    const { handleSubmit, handleChange } = this;
    const {
      referenceNumber,
      documentType,
      companyTaxNumber,
      companyBranch,
      companyNameTH,
      companyNameEN,
      streetAndNumber,
      postCode,
    } = this.state;

    return (
      <ValidatorForm
        ref="form"
        onSubmit={handleSubmit}
        onError={errors => console.log(errors)}
        noValidate
        autoComplete="off"
      >
        <ReferenceNumber
          referenceNumber={referenceNumber}
          handleChange={handleChange}
        />
        <DocumentTypeField
          documentType={documentType}
          handleChange={handleChange}
        />
        <FieldHeader>ข้อมูลบริษัทนำเข้า</FieldHeader>
        <CompanyTaxNumberField
          companyTaxNumber={companyTaxNumber}
          handleChange={handleChange}
        />
        <CompanyBranchField
          companyBranch={companyBranch}
          handleChange={handleChange}
        />
        <CompanyNameFieldTH
          companyNameTH={companyNameTH}
          handleChange={handleChange}
        />
        <CompanyNameFieldEN
          companyNameEN={companyNameEN}
          handleChange={handleChange}
        />
        <FieldHeader>ที่อยู่บริษัทนำเข้า</FieldHeader>
        <AddressField
          streetAndNumber={streetAndNumber}
          handleChange={handleChange}
        />
        <AddressSelectors handleChange={handleChange} />
        <PostCode handleChange={handleChange} postCode={postCode} />
        {this.props.children}
      </ValidatorForm>
    );
  }
}

export default ImportDeclarationControl1;
