import { ValidatorForm } from 'react-material-ui-form-validator';
import validateFloat from './floatFormatValidator';

const addCustomValidationRules = obj => {
  ValidatorForm.addValidationRule(
    'isCorrectWeight',
    value => validateFloat(11, 3, value)
  );

  ValidatorForm.addValidationRule(
    'isMoreThanNet',
    value => {
      const totalNetWeight = parseFloat(
        obj.state.totalNetWeight
      );
      const totalGrossWeight = parseFloat(value);

      return totalGrossWeight < totalNetWeight
        ? false
        : true;
    }
  );

  ValidatorForm.addValidationRule(
    'isCorrectExchangeRate',
    value => validateFloat(12, 5, value)
  );

  ValidatorForm.addValidationRule(
    'isCorrectCIFValue',
    value => validateFloat(16, 2, value)
  );

  ValidatorForm.addValidationRule(
    'isCorrectPaymentAmount',
    value => validateFloat(16, 2, value)
  );
};

export { addCustomValidationRules };
