const fillHouseBillPlaceHolder = value => {
  switch (value) {
    case 1:
    case 2:
      return 'House Bill of Lading';
    case 3.1:
    case 3.2:
    case 7.1:
    case 7.2:
    case 8.1:
    case 8.2:
      return 'Item Number ของ ศบ.1';
    case 4:
      return 'House Airway Bill';
    case 5:
      return 'เลขรายการศุลกากร (Postal Number)';
    case 9:
      return 'เลขบัญชีสินค้า (Passenger Baggage Number)';
    default:
      return '';
  }
};

const setMasterBillAccess = value => {
  const noFill = [1, 5, 9];
  return noFill.includes(value) ? true : false;
};

const fillMasterBillPlaceHolder = value => {
  const noFill = [1, 5, 9];
  if (noFill.includes(value)) {
    return 'ไม่ต้องกรอก';
  } else if (value === 2) {
    return 'Master Bill of Lading';
  } else if (value === 4) {
    return 'Master Airway Bill';
  } else {
    return 'Received Control Number ของ ศบ.1';
  }
};

const fillVesselNamePlaceHolder = value => {
  switch (value) {
    case 1:
      return 'กรอกชื่อเรือ';
    case 2:
      return 'กรอกชื่อรถไฟ';
    case 4:
      return 'กรอกหมายเลขเที่ยวบิน (Flight Number)';
    default:
      return '';
  }
};

const fillVesselName = value => {
  switch (value) {
    case 3.1:
      return 'BY TRUCK';
    case 3.2:
      return 'ON FOOT';
    case 5:
      return 'BY MAIL';
    case 7.1:
    case 7.2:
      return 'BY PIPE';
    case 8.1:
    case 8.2:
      return 'BY BOAT';
    case 9:
      return 'BY PASSENGER';
    default:
      return '';
  }
};

export {
  fillHouseBillPlaceHolder,
  fillMasterBillPlaceHolder,
  fillVesselName,
  fillVesselNamePlaceHolder,
  setMasterBillAccess,
};
