import React, { PureComponent, Fragment } from 'react';
import {
  Dialog,
  Typography,
  IconButton,
  Divider,
  withMobileDialog,
  DialogActions,
  Button,
  DialogContent,
  DialogTitle,
  Tooltip,
} from '@material-ui/core';
import Info from '@material-ui/icons/Info';

class AdditionalInfoDialog extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
    };
  }

  handleClick = () => {
    this.setState(state => ({
      open: !state.open,
    }));
  };

  handleClose = () => {
    this.setState({
      open: false,
    });
  };

  render() {
    return (
      <Fragment>
        <Tooltip title="ข้อมูลเพิ่มเติม">
          <IconButton onClick={this.handleClick}>
            <Info fontSize="small" />
          </IconButton>
        </Tooltip>
        <Dialog
          fullWidth
          scroll="paper"
          fullScreen={this.props.fullScreen}
          maxWidth={this.props.maxWidth}
          open={this.state.open}
          onClose={this.handleClose}
        >
          <DialogTitle>
            <Fragment>
              {this.props.titleTH}

              <Typography
                component="span"
                color="textSecondary"
                variant="subtitle2"
              >
                {this.props.titleEN}
              </Typography>
            </Fragment>
            <Divider style={{ marginTop: 15 }} />
          </DialogTitle>
          <DialogContent>
            <Typography
              color="textSecondary"
              gutterBottom
              variant="subtitle1"
            >
              คำอธิบาย
            </Typography>
            {this.props.children}
            <br />
            <Typography
              gutterBottom
              color="textSecondary"
              variant="subtitle1"
            >
              รูปแบบการกรอก
            </Typography>
            <Typography color="textPrimary">
              {this.props.format}
            </Typography>

            <DialogActions>
              <Button
                variant="outlined"
                size="small"
                color="primary"
                onClick={this.handleClose}
              >
                ปิด
              </Button>
            </DialogActions>
          </DialogContent>
        </Dialog>
      </Fragment>
    );
  }
}

export default withMobileDialog()(AdditionalInfoDialog);
