import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import CalculatorImg from '../../../../images/calculator.png';
import { InputAdornment, Tooltip, IconButton } from '@material-ui/core';

class TotalPaymentAmount extends PureComponent {
  constructor() {
    super();
    this.state = {
      totalPaymentAmount: '',
      hasError: false,
      helperText: 'ยอดชำระเงินรวม',
    };
  }

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  round = value => {
    return Math.round(Math.round(value * 1000) / 10) / 100;
  };

  calculateTotal = (tax = 0, card = 0, deposit = 0) => {
    return this.round(
      parseFloat(tax) - (parseFloat(card) + parseFloat(deposit))
    ).toString();
  };

  handleCalculate = () => {
    let totalTax = document.querySelector('#totalTax').value;
    let cardPaymentAmount = document.querySelector(
      '#totalTaxCardPaymentAmount'
    ).value;
    let totalDeposit = document.querySelector('#totalDeposit').value;

    totalTax = totalTax === '' ? 0 : totalTax;
    cardPaymentAmount = cardPaymentAmount === '' ? 0 : cardPaymentAmount;
    totalDeposit = totalDeposit === '' ? 0 : totalDeposit;

    this.setState({
      totalPaymentAmount: this.calculateTotal(
        totalTax,
        cardPaymentAmount,
        totalDeposit
      ),
    });
  };

  render() {
    const { handleChange } = this;
    const { totalPaymentAmount } = this.state;
    return (
      <TextValidator
        required
        name="totalPaymentAmount"
        id="totalPaymentAmount"
        onChange={handleChange}
        value={totalPaymentAmount}
        label="Total Payment Amount"
        helperText="ยอดชำระเงินรวม"
        placeholder="0000.00"
        fullWidth
        validators={['required', 'isFloat', 'isCorrectPaymentAmount']}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'ต้องเป็นตัวเลขและจุดทศนิยมเท่านั้น',
          'กรอกเป็นตัวเลข 16 ตัว รวมจุดทศนิยม 2 ตำแหน่ง',
        ]}
        margin="dense"
        variant="outlined"
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <Tooltip title="คำนวณ" placement="left">
                <IconButton onClick={this.handleCalculate}>
                  <img
                    alt="calculateBtn"
                    width="16.67"
                    height="16.67"
                    src={CalculatorImg}
                  />
                </IconButton>
              </Tooltip>
            </InputAdornment>
          ),
        }}
      />
    );
  }
}

export default TotalPaymentAmount;
