import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const BrokerBranch = React.memo(props => {
  const { handleChange, brokerBranch } = props;
  return (
    <TextValidator
      name="brokerBranch"
      id="brokerBranch"
      onChange={handleChange}
      value={brokerBranch}
      label="Broker Branch"
      helperText="สาขาที่อยู่ของ Broker | ไม่มีสาขาให้กรอก 0"
      fullWidth
      validators={['matchRegexp:^[0-9]*$', 'maxStringLength:6']}
      errorMessages={['กรอกได้เฉพาะตัวเลข 0-9', 'ต้องกรอกไม่เกิน 6 หลัก']}
      variant="outlined"
      margin="dense"
    />
  );
});

export default BrokerBranch;
