import React, { PureComponent } from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';
import {
  InputAdornment,
  IconButton,
  Tooltip,
  Typography,
} from '@material-ui/core';
import AdditionalInfoDialog from '../../../shared/AdditionalInfoDialog';
import CalculatorImg from '../../../../images/calculator.png';

class CIFValueBaht extends PureComponent {
  state = {
    cifValueBaht: '',
    hasError: false,
    helperText: 'ราคา CIF เงินบาท',
  };

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  multiplyFloat = (first, second) => {
    const result = parseFloat(first) * parseFloat(second);
    const roundedResult = Math.round(Math.round(result * 1000) / 10) / 100;
    return roundedResult.toString();
  };

  displayError = () => {
    this.setState({
      helperText: 'กรุณากรอกช่อง Exchange Rate และ CIF Value Foreign',
      hasError: true,
      cifValueBaht: '',
    });
  };

  getCIFValueBaht = () => {
    if (!this.props.exchangeRateRef || !this.props.CIFForeignRef) {
      this.displayError();
      return;
    }

    const exchangeRate = this.props.exchangeRateRef.state.exchangeRate;
    const cifValueForeign = this.props.CIFForeignRef.props.cifValueForeign;

    if (exchangeRate.length > 0 && cifValueForeign.length > 0) {
      this.setState({
        cifValueBaht: this.multiplyFloat(exchangeRate, cifValueForeign),
        hasError: false,
        helperText: 'ราคา CIF เงินบาท',
      });
    } else {
      this.displayError();
    }
  };

  render() {
    const { handleChange } = this;
    const { cifValueBaht, hasError, helperText } = this.state;
    return (
      <TextValidator
        required
        error={hasError}
        name="cifValueBaht"
        id="cifValueBaht"
        onChange={handleChange}
        value={cifValueBaht}
        label="CIF Value Baht"
        helperText={helperText}
        placeholder="0000.00"
        fullWidth
        validators={['required', 'isFloat', 'isCorrectCIFValue']}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'ต้องเป็นตัวเลขและจุดทศนิยมเท่านั้น',
          'กรอกเป็นตัวเลขสูงสุด 16 หลัก รวมจุดทศนิยมสูงสุด 2 ตำแหน่ง',
        ]}
        margin="dense"
        variant="outlined"
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <Tooltip title="คำนวณ" placement="left">
                <IconButton onClick={this.getCIFValueBaht}>
                  <img
                    alt="calculateBtn"
                    width="16.67"
                    height="16.67"
                    src={CalculatorImg}
                  />
                </IconButton>
              </Tooltip>
              <AdditionalInfoDialog
                titleTH="ราคา CIF เงินบาท"
                titleEN="CIF Value Baht"
                format="n..16,2"
                maxWidth="xs"
              >
                <Typography>
                  มีค่าเท่ากับ Exchange Rate คูณ CIF Value Foreign
                </Typography>
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
      />
    );
  }
}

export default CIFValueBaht;
