import React, { Fragment } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const ManagerID = React.memo(props => {
  const { handleChange, managerId } = props;
  return (
    <Fragment>
      <TextValidator
        name="managerId"
        id="managerId"
        required
        onChange={handleChange}
        value={managerId}
        label="Manager ID Card"
        helperText="เลขบัตรประชาชนผู้จัดการ / ผู้รับมอบอำนาจของบริษัทหรือ Broker"
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[A-Za-z0-9]*$',
          'maxStringLength:17',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'กรอกเฉพาะตัวอักษรหรือตัวเลข',
          'ต้องกรอกไม่เกิน 17 หลัก',
        ]}
        variant="outlined"
        margin="dense"
      />
    </Fragment>
  );
});

export default ManagerID;
