import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const TotalTaxCardPaymentAmount = React.memo(props => {
  const { handleChange, totalTaxCardPaymentAmount } = props;
  return (
    <TextValidator
      required
      name="totalTaxCardPaymentAmount"
      id="totalTaxCardPaymentAmount"
      onChange={handleChange}
      value={totalTaxCardPaymentAmount}
      label="Total Tax Card Payment Amount"
      helperText="ยอดชำระเงินรวม ที่ต้องการตัดบัญชีเดบิตบัตรภาษี"
      placeholder="0000.00"
      fullWidth
      validators={['required', 'isFloat', 'isCorrectPaymentAmount']}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'ต้องเป็นตัวเลขและจุดทศนิยมเท่านั้น',
        'กรอกเป็นตัวเลข 16 ตัว รวมจุดทศนิยม 2 ตำแหน่ง',
      ]}
      margin="dense"
      variant="outlined"
    />
  );
});

export default TotalTaxCardPaymentAmount;
