import React, { PureComponent, Fragment } from 'react';
import { ValidatorForm } from 'react-material-ui-form-validator';
import FieldHeader from '../shared/FieldHeader';
import { addCustomValidationRules } from '../shared/customValidations';
import AssessmentRequestCode from './fields/idc5Fields/AssessmentRequestCode';
import InspectionRequestCode from './fields/idc5Fields/InspectionRequestCode';
import DepartureDate from './fields/idc4Fields/DepartureDate';
import SubBrokerTaxNumber from './fields/idc5Fields/SubBrokerTaxNumber';
import RadioControl from './fields/idc4Fields/RadioControl';
import ApprovalPort from './fields/idc5Fields/ApprovalPort';
import ApprovalNumber from './fields/idc5Fields/ApprovalNumber';
import RegistrationID from './fields/idc5Fields/RegistrationID';

class ImportDeclarationControl5 extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      assessmentRequestCode: '',
      inspectionRequestCode: '',
      exportTaxIncentivesID: '',
      subBrokerTaxNumber: '',
      registrationID: '',
    };
  }

  componentWillMount() {
    addCustomValidationRules(this);
  }

  saveToLocalStorage = data => {
    for (const pair of data.entries()) {
      localStorage.setItem(pair[0], pair[1]);
    }
  };

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    const data = new FormData(e.target);
    this.saveToLocalStorage(data);
    this.props.handlenext();
  };

  render() {
    const { handleChange, handleSubmit } = this;
    const {
      assessmentRequestCode,
      inspectionRequestCode,
      subBrokerTaxNumber,
      registrationID,
    } = this.state;
    return (
      <Fragment>
        <ValidatorForm
          onSubmit={handleSubmit}
          noValidate
          autoComplete="off"
        >
          <FieldHeader>อื่นๆ</FieldHeader>
          <AssessmentRequestCode
            handleChange={handleChange}
            assessmentRequestCode={assessmentRequestCode}
          />
          <InspectionRequestCode
            handleChange={handleChange}
            inspectionRequestCode={inspectionRequestCode}
          />
          <DepartureDate />
          <SubBrokerTaxNumber
            handleChange={handleChange}
            subBrokerTaxNumber={subBrokerTaxNumber}
          />
          <RegistrationID
            handleChange={handleChange}
            registrationID={registrationID}
          />
          <RadioControl
            formLabel="ใช้สิทธิประโยชน์ใบขนสินค้าหลายเที่ยวเรือ"
            radioName="useMultipleShipments"
            yesLabel="ใช้"
            noLabel="ไม่ใช้"
          >
            <ApprovalPort />
            <ApprovalNumber />
          </RadioControl>
          {this.props.children}
        </ValidatorForm>
      </Fragment>
    );
  }
}

export default ImportDeclarationControl5;
