import React, { PureComponent } from 'react';
import SelectValidator from 'react-material-ui-form-validator/lib/SelectValidator';
import { MenuItem } from '@material-ui/core';

const paymentMethods = [
  {
    value: 'A',
    label: 'A - ชำระที่กรมศุลกากร',
  },
  {
    value: 'H',
    label: 'H - ชำระผ่านธนาคาร',
  },
  {
    value: 'L',
    label: 'L - ไม่มีการชำระ',
  },
];

class PaymentMethod extends PureComponent {
  render() {
    const { handleChange, paymentMethod } = this.props;

    return (
      <SelectValidator
        id="paymentMethod"
        required
        name="paymentMethod"
        onChange={handleChange}
        value={paymentMethod}
        select
        label="Payment Method"
        helperText="วิธีชำระภาษีอากร"
        fullWidth
        validators={['required']}
        errorMessages={['จำเป็นต้องเลือก']}
        margin="dense"
        variant="outlined"
      >
        {paymentMethods.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </SelectValidator>
    );
  }
}

export default PaymentMethod;
