import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CompanyNameFieldTH = React.memo(props => {
  const { handleChange, companyNameTH } = props;
  return (
    <TextValidator
      name="companyNameTH"
      id="companyNameTH"
      required
      onChange={handleChange}
      value={companyNameTH}
      label="ชื่อบริษัทภาษาไทย"
      placeholder="บริษัท เฮฟวี่สโตนเอ็กซ์เพรส จำกัด"
      fullWidth
      validators={[
        'required',
        'matchRegexp:^[^A-Za-z]*$',
        'maxStringLength:120',
      ]}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'กรุณากรอกเป็นภาษาไทยล้วน',
        'ความยาวสูงสุด 120 ตัวอักษร',
      ]}
      variant="outlined"
      margin="dense"
    />
  );
});

export default CompanyNameFieldTH;
