import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CompanyNameFieldEN = React.memo(props => {
  const { handleChange, companyNameEN } = props;
  return (
    <TextValidator
      name="companyNameEN"
      id="companyNameEN"
      required
      onChange={handleChange}
      value={companyNameEN}
      label="ชื่อบริษัทภาษาอังกฤษ"
      placeholder="Heavy Stone Express Co., Ltd."
      fullWidth
      validators={[
        'required',
        'matchRegexp:^[^ขฃฉฐถผฝศษสหกจฎฏดตบปอคฅฆงชซฌญฑฒณทธนพฟภมยรลวฬฮ]*$',
        'maxStringLength:70',
      ]}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'กรุณากรอกเป็นภาษาอังกฤษล้วน',
        'ความยาวสูงสุด 70 ตัวอักษร',
      ]}
      variant="outlined"
      margin="dense"
    />
  );
});

export default CompanyNameFieldEN;
