import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const PostCode = React.memo(props => {
  const { postCode, handleChange } = props;
  return (
    <TextValidator
      id="postCode"
      required
      name="postCode"
      onChange={handleChange}
      placeholder="00000"
      value={postCode}
      label="รหัสไปรษณีย์"
      fullWidth
      validators={['required', 'maxStringLength:9']}
      errorMessages={['จำเป็นต้องกรอก', 'ความยาวสูงสุด 9 ตัวอักษร']}
      variant="outlined"
      margin="dense"
    />
  );
});

export default PostCode;
