import React, { PureComponent, Fragment } from 'react';
import { ValidatorForm } from 'react-material-ui-form-validator';
import FieldHeader from '../shared/FieldHeader';
import { addCustomValidationRules } from '../shared/customValidations';
import PaymentMethod from './fields/idc4Fields/PaymentMethod';
import GuaranteeMethod from './fields/idc4Fields/GuaranteeMethod';
import guaranteeMethodsData from './fields/idc4Fields/data/guaranteeMethodsData';
import guaranteeTypesData from './fields/idc4Fields/data/guaranteeTypesData';
import GuaranteeType from './fields/idc4Fields/GuaranteeType';
import BankFields from './fields/idc4Fields/BankFields';
import GuaranteeBankCode from './fields/idc4Fields/GuaranteeBankCode';
import GuaranteeBankBranchCode from './fields/idc4Fields/GuaranteeBankBranchCode';
import GuaranteeBankAccountNumber from './fields/idc4Fields/GuaranteeBankAccountNumber';
import TotalDepositAmount from './fields/idc4Fields/TotalDepositAmount';
import TotalTax from './fields/idc4Fields/TotalTax';
import TotalDeposit from './fields/idc4Fields/TotalDeposit';

class ImportDeclarationControl4 extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      paymentMethod: '',
      guaranteeMethod: '',
      guaranteeMethodData: [],
      guaranteeTypeData: [],
      guaranteeType: [],
      guaranteeBankCode: '',
      guaranteeBankBranchCode: '',
      guaranteeBankAccountNumber: '',
      totalDepositAmount: '',
      totalTax: '',
      totalDeposit: '',
    };
    this.paymentRef = React.createRef();
  }

  componentWillMount() {
    addCustomValidationRules(this);
  }

  saveToLocalStorage = data => {
    for (const pair of data.entries()) {
      localStorage.setItem(pair[0], pair[1]);
    }
  };

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  chooseDataValue = choosingArray => {
    this.setState({
      guaranteeTypeData: guaranteeTypesData.filter(val =>
        choosingArray.includes(val.value)
      ),
    });
  };

  filterGuaranteeType = e => {
    const { chooseDataValue } = this;
    const guaranteeMethod = e.target.value;
    const payment = this.paymentRef.current.props.paymentMethod;

    if (guaranteeMethod === 'L') {
      this.setState({
        guaranteeTypeData: [],
      });
      return;
    }

    if (payment === 'A') {
      if (guaranteeMethod === 'A') {
        chooseDataValue(['C', 'B', 'M']);
      } else if (guaranteeMethod === 'H') {
        chooseDataValue(['E']);
      }
    } else if (payment === 'H') {
      if (guaranteeMethod === 'H') {
        chooseDataValue(['C', 'E']);
      }
    } else if (payment === 'L') {
      if (guaranteeMethod === 'A') {
        chooseDataValue(['C', 'B', 'M']);
      } else if (guaranteeMethod === 'H') {
        chooseDataValue(['E', 'C']);
      }
    }
  };

  filterGuaranteeMethods = e => {
    if (e.target.value === 'H') {
      this.setState({
        guaranteeMethodData: guaranteeMethodsData.filter(
          data => data.value !== 'A'
        ),
      });
    } else {
      this.setState({
        guaranteeMethodData: guaranteeMethodsData,
      });
    }
  };

  handleGuaranteeMethodChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
    this.filterGuaranteeType(e);
  };

  handlePaymentMethodChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
    this.filterGuaranteeMethods(e);
  };

  handleSubmit = e => {
    e.preventDefault();
    const data = new FormData(e.target);
    this.saveToLocalStorage(data);
    this.props.handlenext();
  };

  render() {
    const {
      handleChange,
      handleSubmit,
      handlePaymentMethodChange,
      handleGuaranteeMethodChange,
    } = this;
    const {
      paymentMethod,
      guaranteeMethod,
      guaranteeMethodData,
      guaranteeType,
      guaranteeTypeData,
      guaranteeBankCode,
      guaranteeBankBranchCode,
      guaranteeBankAccountNumber,
      totalDepositAmount,
      totalTax,
      totalDeposit,
    } = this.state;

    return (
      <Fragment>
        <ValidatorForm
          onSubmit={handleSubmit}
          noValidate
          autoComplete="off"
        >
          <FieldHeader>รูปแบบการชำระเงิน</FieldHeader>
          <PaymentMethod
            ref={this.paymentRef}
            handleChange={handlePaymentMethodChange}
            paymentMethod={paymentMethod}
          />
          <GuaranteeMethod
            handleChange={handleGuaranteeMethodChange}
            guaranteeMethod={guaranteeMethod}
            guaranteeMethodData={guaranteeMethodData}
          />
          <GuaranteeType
            handleChange={handleChange}
            guaranteeType={guaranteeType}
            guaranteeTypeData={guaranteeTypeData}
          />
          {/* Payment Fields */}
          <FieldHeader>การชำระค่าภาษี</FieldHeader>
          <TotalTax handleChange={handleChange} totalTax={totalTax} />
          <TotalDeposit
            handleChange={handleChange}
            totalDeposit={totalDeposit}
          />
          <BankFields
            handleChange={handleChange}
            guaranteeType={guaranteeType}
            guaranteeMethod={guaranteeMethod}
            paymentMethod={paymentMethod}
          />

          {/* Guarantee Fields */}
          {guaranteeType === 'E' && (
            <Fragment>
              <FieldHeader>ธนาคารที่ใช้ค้ำประกัน</FieldHeader>
              <GuaranteeBankCode
                handleChange={handleChange}
                guaranteeBankCode={guaranteeBankCode}
              />
              <GuaranteeBankBranchCode
                handleChange={handleChange}
                guaranteeBankBranchCode={guaranteeBankBranchCode}
              />
              <GuaranteeBankAccountNumber
                handleChange={handleChange}
                guaranteeBankAccountNumber={guaranteeBankAccountNumber}
              />
              <TotalDepositAmount
                handleChange={handleChange}
                totalDepositAmount={totalDepositAmount}
              />
            </Fragment>
          )}
          {this.props.children}
        </ValidatorForm>
      </Fragment>
    );
  }
}

export default ImportDeclarationControl4;
