import React, { PureComponent, Fragment } from 'react';
import BrokerTaxNumber from './fields/idc2Fields/BrokerTaxNumber';
import { ValidatorForm } from 'react-material-ui-form-validator';
import BrokerBranch from './fields/idc2Fields/BrokerBranch';
import CustomsClearanceID from './fields/idc2Fields/CustomsClearanceId';
import CustomsClearanceName from './fields/idc2Fields/CustomsClearanceName';
import ManagerID from './fields/idc2Fields/ManagerID';
import ManagerName from './fields/idc2Fields/ManagerName';
import ModeOfTransport from './fields/idc2Fields/ModeOfTransport';
import CargoTypeCode from './fields/idc2Fields/CargoTypeCode';
import VesselName from './fields/idc2Fields/VesselName';
import ArrivalDate from './fields/idc2Fields/ArrivalDate';
import MasterBillOfLading from './fields/idc2Fields/MasterBillOfLading';
import HouseBillOfLading from './fields/idc2Fields/HouseBillOfLading';
import {
  fillHouseBillPlaceHolder,
  fillMasterBillPlaceHolder,
  fillVesselName,
  fillVesselNamePlaceHolder,
  setMasterBillAccess,
} from '../helperFunctions/billOfLadingFunctions';
import OutsideReleasePort from './fields/idc2Fields/OutsideReleasePort';
import ReleasePort from './fields/idc2Fields/ReleasePort';
import DischargePort from './fields/idc2Fields/DischargePort';
import FieldHeader from '../shared/FieldHeader';
import OriginCountryCode from './fields/idc2Fields/OriginCountryCode';
import ConsignmentCountryCode from './fields/idc2Fields/ConsignmentCountryCode';

class ImportDeclarationControl2 extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      brokerTaxNumber: '',
      brokerBranch: '',
      customsClearanceId: '',
      customsClearanceName: '',
      managerId: '',
      managerName: '',
      modeOfTransport: '',
      cargoTypeCode: '',
      vesselName: '',
      vesselNamePlaceholer: '',
      arrivalDate: '',
      masterBillOfLading: '',
      masterBill: {
        placeholder: '',
        disabled: false,
        required: true,
      },
      houseBillOfLading: '',
      houseBillPlaceHolder: '',
      outsideReleasePort: '',
      releasePort: '',
      dischargePort: '',
    };
  }

  saveToLocalStorage = data => {
    for (const pair of data.entries()) {
      localStorage.setItem(pair[0], pair[1]);
    }
  };

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  handleModeOfTransportChange = e => {
    const value = e.target.value;
    this.setState({
      [e.target.name]: value,
      vesselName: fillVesselName(value),
      vesselNamePlaceholer: fillVesselNamePlaceHolder(value),
      masterBill: {
        placeholder: fillMasterBillPlaceHolder(value),
        disabled: setMasterBillAccess(value),
        required: !setMasterBillAccess(value),
      },
      houseBillPlaceHolder: fillHouseBillPlaceHolder(value),
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    const data = new FormData(e.target);
    this.saveToLocalStorage(data);
    this.props.handlenext();
  };

  render() {
    const {
      brokerTaxNumber,
      brokerBranch,
      customsClearanceId,
      customsClearanceName,
      managerId,
      managerName,
      modeOfTransport,
      cargoTypeCode,
      vesselName,
      vesselNamePlaceholer,
      masterBillOfLading,
      masterBill,
      houseBillOfLading,
      houseBillPlaceHolder,
      outsideReleasePort,
      releasePort,
      dischargePort,
    } = this.state;

    const {
      handleChange,
      handleSubmit,
      handleModeOfTransportChange,
    } = this;

    return (
      <Fragment>
        <ValidatorForm
          onSubmit={handleSubmit}
          noValidate
          autoComplete="off"
        >
          <FieldHeader>ข้อมูลเกี่ยวกับ Broker</FieldHeader>
          <BrokerTaxNumber
            handleChange={handleChange}
            brokerTaxNumber={brokerTaxNumber}
          />
          {brokerTaxNumber.length > 0 && (
            <BrokerBranch
              handleChange={handleChange}
              brokerBranch={brokerBranch}
            />
          )}
          <CustomsClearanceID
            handleChange={handleChange}
            customsClearanceId={customsClearanceId}
          />
          <CustomsClearanceName
            handleChange={handleChange}
            customsClearanceName={customsClearanceName}
          />
          <ManagerID handleChange={handleChange} managerId={managerId} />
          <ManagerName
            handleChange={handleChange}
            managerName={managerName}
          />
          <FieldHeader>การขนส่งและยานพาหนะ</FieldHeader>
          <ModeOfTransport
            handleChange={handleModeOfTransportChange}
            modeOfTransport={modeOfTransport}
          />
          <CargoTypeCode
            handleChange={handleChange}
            cargoTypeCode={cargoTypeCode}
          />
          <VesselName
            placeholder={vesselNamePlaceholer}
            handleChange={handleChange}
            vesselName={vesselName}
          />
          <FieldHeader>รายละเอียดการขนส่ง</FieldHeader>
          <ArrivalDate />
          <MasterBillOfLading
            placeholder={masterBill.placeholder}
            handleChange={handleChange}
            masterBillOfLading={masterBillOfLading}
            isDisabled={masterBill.disabled}
            isRequired={masterBill.required}
          />
          <HouseBillOfLading
            handleChange={handleChange}
            houseBillOfLading={houseBillOfLading}
            placeholder={houseBillPlaceHolder}
          />
          <OutsideReleasePort
            handleChange={handleChange}
            outsideReleasePort={outsideReleasePort}
          />
          <ReleasePort
            handleChange={handleChange}
            releasePort={releasePort}
          />
          <DischargePort
            handleChange={handleChange}
            dischargePort={dischargePort}
          />
          <OriginCountryCode />
          <ConsignmentCountryCode />
          {this.props.children}
        </ValidatorForm>
      </Fragment>
    );
  }
}

export default ImportDeclarationControl2;
