import React, { Fragment, PureComponent } from 'react';
import { DatePicker } from 'material-ui-pickers';
import { InputAdornment, Typography } from '@material-ui/core';
import AdditionalInfoDialog from '../../../shared/AdditionalInfoDialog';

class DepartureDate extends PureComponent {
  state = {
    selectedDate: new Date(),
    documentType: '',
  };

  componentDidMount() {
    this.setState({
      documentType: localStorage.getItem('documentType'),
    });
  }

  handleDateChange = date => {
    this.setState({
      selectedDate: date,
    });
  };

  render() {
    const { selectedDate, documentType } = this.state;
    const docTypesToShow = ['A', 'C', 'P'];

    return docTypesToShow.includes(documentType) ? (
      <Fragment>
        <DatePicker
          id="departureDate"
          label="Departure Date"
          format="yyyy/MM/dd"
          placeholder="2018/12/31"
          name="departureDate"
          fullWidth
          value={selectedDate}
          onChange={this.handleDateChange}
          adornmentPosition="start"
          margin="dense"
          variant="outlined"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <AdditionalInfoDialog
                  maxWidth="sm"
                  titleTH="วันที่นําสินค้าออกจาก Bond/Free Zone/I-EAT Free Zone"
                  titleEN="Departure Date"
                  format="n8"
                >
                  <Typography>
                    ต้องบันทึกค่าเมื่อ Document Type เท่ากับ A -
                    ใบขนสนค้าขาเข้าโอนย้ายภายในประเทศ หรือ C -
                    ใบขนสนค้าขาเข้าโอนย้ายจากเขตปลอดอากร / เขตประกอบการเสรี
                    P - ใบขนสินค้าขาเข้าโอนย้าย ชำระค่าภาษีอากร
                  </Typography>
                </AdditionalInfoDialog>
              </InputAdornment>
            ),
          }}
          keyboard
          mask={value =>
            value
              ? [/\d/, /\d/, /\d/, /\d/, '/', /\d/, /\d/, '/', /\d/, /\d/]
              : []
          }
        />
      </Fragment>
    ) : (
      ''
    );
  }
}

export default DepartureDate;
