import React, { PureComponent } from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';
import {
  InputAdornment,
  IconButton,
  Tooltip,
  Typography,
} from '@material-ui/core';
import AdditionalInfoDialog from '../../../shared/AdditionalInfoDialog';
import exchangeRateData from '../../../../jsonData/exchangeRateData.json';
import AutoRenewIcon from '@material-ui/icons/Autorenew';

class ExchangeRate extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      exchangeRate: '',
    };
  }

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  getExchangeRate = () => {
    const currency = document.querySelector('#currencyCode');
    const currencyObj = exchangeRateData.find(
      element => element.name === currency.value
    );
    currencyObj
      ? this.setState({
          exchangeRate: parseFloat(currencyObj.value).toString(),
        })
      : this.setState({
          exchangeRate: '',
        });
  };

  render() {
    return (
      <TextValidator
        required
        name="exchangeRate"
        id="exchangeRate"
        onChange={this.handleChange}
        value={this.state.exchangeRate}
        label="Exchange Rate"
        helperText="อัตราแลกเปลี่ยนเงินตรา"
        placeholder="0000.00000"
        fullWidth
        validators={['required', 'isFloat', 'isCorrectExchangeRate']}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'ต้องเป็นตัวเลขและจุดทศนิยมเท่านั้น',
          'กรุณากรอกตามรูปแบบ (คลิกไอคอน i เพื่อดูเพิ่มเติม)',
        ]}
        margin="dense"
        variant="outlined"
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <Tooltip title="ดึงค่าอัตราแลกเปลี่ยน">
                <IconButton onClick={this.getExchangeRate}>
                  <AutoRenewIcon />
                </IconButton>
              </Tooltip>
              <AdditionalInfoDialog
                titleTH="อัตราแลกเปลี่ยนเงินตรา"
                titleEN="Exchange Rate"
                format="n..12,5"
                maxWidth="xs"
              >
                <Typography gutterBottom>
                  ข้อมูลอัตราแลกเปลี่ยนเดือนมกราคม 2019
                </Typography>
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
      />
    );
  }
}

export default ExchangeRate;
