import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CompanyTaxNumberField = React.memo(props => {
  const { handleChange, companyTaxNumber } = props;
  return (
    <TextValidator
      name="companyTaxNumber"
      id="companyTaxNumber"
      required
      onChange={handleChange}
      value={companyTaxNumber}
      label="เลขประจําตัวผู้เสียภาษีอากร"
      fullWidth
      validators={[
        'required',
        'matchRegexp:^[A-Za-z0-9]*$',
        'maxStringLength:17',
      ]}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'กรอกได้เฉพาะตัวอักษร A-Z และเลข 0-9',
        'ต้องกรอกไม่เกิน 17 หลัก',
      ]}
      variant="outlined"
      margin="dense"
    />
  );
});

export default CompanyTaxNumberField;
