import React from 'react';
import { SelectValidator } from 'react-material-ui-form-validator';
import { MenuItem } from '@material-ui/core';

const documentTypes = [
  {
    value: 0,
    label: '0 – ใบขนสินค้าขาเข้า',
  },
  {
    value: 3,
    label: '3 – คําร้องขอรับของไปก่อน',
  },
  {
    value: 5,
    label: '5 - ใบขนสินค้าเข้าปากระวาง',
  },
  {
    value: 'X',
    label: 'X – ใบขนสินค้าขาเข้าเร่งด่วน (Express)',
  },
];

const DocumentTypeField = React.memo(props => {
  const { handleChange, documentType } = props;
  return (
    <SelectValidator
      id="documentType"
      required
      name="documentType"
      onChange={handleChange}
      value={documentType}
      select
      label="ชนิดเอกสาร"
      fullWidth
      validators={['required']}
      errorMessages={['จำเป็นต้องเลือก']}
      variant="outlined"
      margin="dense"
    >
      {documentTypes.map(option => (
        <MenuItem key={option.value} value={option.value}>
          {option.label}
        </MenuItem>
      ))}
    </SelectValidator>
  );
});

export default DocumentTypeField;
