import React, { Fragment } from 'react';
import SelectValidator from 'react-material-ui-form-validator/lib/SelectValidator';
import { MenuItem } from '@material-ui/core';

const GuaranteeMethod = React.memo(props => {
  const { handleChange, guaranteeMethod, guaranteeMethodData } = props;
  return (
    <Fragment>
      {guaranteeMethodData.length === 0 ? (
        ''
      ) : (
        <SelectValidator
          id="guaranteeMethod"
          required
          name="guaranteeMethod"
          onChange={handleChange}
          value={guaranteeMethod}
          select
          label="Guarantee Method"
          helperText="วิธีการวางประกัน"
          fullWidth
          validators={['required']}
          errorMessages={['จำเป็นต้องเลือก']}
          margin="dense"
          variant="outlined"
        >
          {guaranteeMethodData.map(option => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}
        </SelectValidator>
      )}
    </Fragment>
  );
});

export default GuaranteeMethod;
