import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CompanyBranchField = React.memo(props => {
  const { handleChange, companyBranch } = props;
  return (
    <TextValidator
      name="companyBranch"
      id="companyBranch"
      required
      type="number"
      onChange={handleChange}
      value={companyBranch}
      label="สาขาที่อยู่ของบริษัทผู้นำเข้า"
      placeholder="000000"
      fullWidth
      validators={[
        'required',
        'matchRegexp:^[0-9]*$',
        'maxStringLength:6',
      ]}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'กรอกได้เฉพาะตัวเลข 0-9',
        'ต้องกรอกไม่เกิน 6 หลัก',
      ]}
      variant="outlined"
      margin="dense"
    />
  );
});

export default CompanyBranchField;
