import React from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';

const BankBranchCode = React.memo(props => {
  const { handleChange, bankBranchCode } = props;
  return (
    <TextValidator
      required
      name="bankBranchCode"
      id="bankBranchCode"
      onChange={handleChange}
      value={bankBranchCode}
      label="Bank Branch Code"
      helperText="รหัสสาขาธนาคารของผู้นำเข้า"
      placeholder="000000"
      fullWidth
      validators={['required', 'maxStringLength: 6']}
      errorMessages={['จำเป็นต้องกรอก', 'กรอกได้สูงสุด 6 หลัก']}
      margin="dense"
      variant="outlined"
    />
  );
});

export default BankBranchCode;
