import React, { Fragment, PureComponent } from 'react';
import { DatePicker } from 'material-ui-pickers';
import { InputAdornment } from '@material-ui/core';
import AdditionalInfoDialog from '../../../shared/AdditionalInfoDialog';
import ExpansionCards from '../../../shared/ExpansionCards';
import { arrivalDateData } from './infoBody/arrivalDateData';

class ArrivalDate extends PureComponent {
  state = {
    selectedDate: new Date(),
    selectedDateString: '',
    toggleTable: false,
  };

  handleDateChange = date => {
    this.setState({
      selectedDate: date,
    });
  };

  render() {
    const { selectedDate } = this.state;

    return (
      <Fragment>
        <DatePicker
          id="arrivalDate"
          label="Arrival Date"
          format="yyyy/MM/dd"
          placeholder="2018/12/31"
          name="arrivalDate"
          fullWidth
          value={selectedDate}
          onChange={this.handleDateChange}
          adornmentPosition="start"
          variant="outlined"
          margin="dense"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <AdditionalInfoDialog
                  titleTH="วันที่นำเข้า"
                  titleEN="Arrival Date"
                  format="n8"
                >
                  {arrivalDateData.map(data => (
                    <Fragment key={data.modeName}>
                      <ExpansionCards
                        icon={data.icon}
                        titleName={data.modeName}
                        explanation={data.details}
                      />
                    </Fragment>
                  ))}
                </AdditionalInfoDialog>
              </InputAdornment>
            ),
          }}
          keyboard
          mask={value =>
            value
              ? [/\d/, /\d/, /\d/, /\d/, '/', /\d/, /\d/, '/', /\d/, /\d/]
              : []
          }
        />
      </Fragment>
    );
  }
}

export default ArrivalDate;
