import React, { Fragment, PureComponent } from 'react';
import tambons from '../../../../jsonData/tambon';
import provinces from '../../../../jsonData/province';
import amphoes from '../../../../jsonData/amphoe';
import { TextValidator } from 'react-material-ui-form-validator';

import TambonSelect from './TambonSelection';

class AddressSelectors extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      selectedTambon: '',
      selectedAmphoe: '',
      selectedProvince: '',
      searchInput: '',
      selected: false,
      anchorEl: '',
      anchorElWidth: '',
      filteredTambons: [],
    };
  }

  handleSeachInput = e => {
    const { target, currentTarget } = e;
    this.setState({
      selected: false,
      searchInput: target.value,
      anchorEl: currentTarget,
      anchorElWidth: currentTarget.offsetWidth,
      filteredTambons: tambons.filter(tambon =>
        tambon.name.includes(target.value)
      ),
    });
  };

  handleChange = name => e => {
    this.setState({
      [name]: e.target.value,
    });
  };

  updateSelectedTambon = e => {
    this.setState({
      selectedTambon: tambons.find(
        tambon => tambon.name === e.target.innerText.trim()
      ),
    });
  };

  updateSelectedAmphoe = () => {
    this.setState(state => ({
      selectedAmphoe: amphoes.find(
        amphoe => amphoe.pid === state.selectedTambon.amphoe_pid
      ).name,
    }));
  };

  updateSelectedProvince = () => {
    this.setState(state => ({
      selectedProvince: provinces.find(
        province => province.pid === state.selectedTambon.changwat_pid
      ).name,
    }));
  };

  handleSelect = e => {
    this.updateSelectedTambon(e);
    this.updateSelectedAmphoe();
    this.updateSelectedProvince();
    this.setState({
      selected: true,
      searchInput: e.target.innerText.trim(),
    });
  };

  render() {
    const {
      searchInput,
      selected,
      selectedAmphoe,
      filteredTambons,
      selectedProvince,
      anchorEl,
      anchorElWidth,
    } = this.state;
    const { handleSelect, handleSeachInput, handleChange } = this;

    return (
      <Fragment>
        <TextValidator
          id="companyTambon"
          name="companyTambon"
          fullWidth
          label="ตำบล / แขวง"
          placeholder="กรอกชื่อตำบล / แขวง เพื่อค้นหา"
          helperText="ไม่มีให้เว้นว่าง"
          type="search"
          value={searchInput}
          onChange={handleSeachInput}
          validators={['maxStringLength:35']}
          errorMessages={['ความยาวสูงสุด 35 ตัวอักษร']}
          variant="outlined"
          margin="dense"
        />
        <TambonSelect
          anchorElWidth={anchorElWidth}
          anchorEl={anchorEl}
          searchInput={searchInput}
          selected={selected}
          handleSelect={handleSelect}
          filteredTambons={filteredTambons}
        />
        <TextValidator
          id="subProvince"
          name="subProvince"
          onChange={handleChange('selectedAmphoe')}
          value={selectedAmphoe}
          helperText="กรุณาเลือกตำบล/แขวงก่อน | ไม่มีให้เว้นว่าง"
          label="อำเภอ / เขต"
          fullWidth
          validators={['maxStringLength:35']}
          errorMessages={['ความยาวสูงสุด 35 ตัวอักษร']}
          variant="outlined"
          margin="dense"
        />
        <TextValidator
          id="province"
          name="province"
          onChange={handleChange('selectedProvince')}
          value={selectedProvince}
          helperText="กรุณาเลือกตำบล/แขวงก่อน | ไม่มีให้เว้นว่าง"
          label="จังหวัด"
          fullWidth
          validators={['maxStringLength:35']}
          errorMessages={['ความยาวสูงสุด 35 ตัวอักษร']}
          variant="outlined"
          margin="dense"
        />
      </Fragment>
    );
  }
}

export default AddressSelectors;
