import React from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';

const BankCode = React.memo(props => {
  const { handleChange, bankCode } = props;
  return (
    <TextValidator
      required
      name="bankCode"
      id="bankCode"
      onChange={handleChange}
      value={bankCode}
      label="Bank Code"
      helperText="รหัสธนาคารของผู้นําเข้าที่ใช้หักบัญชีจ่ายภาษี"
      placeholder="000"
      fullWidth
      validators={['required', 'isNumber', 'maxStringLength: 3']}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'ต้องเป็นตัวเลขเท่านั้น',
        'กรอกได้สูงสุด 3 หลัก',
      ]}
      margin="dense"
      variant="outlined"
    />
  );
});

export default BankCode;
