import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

class ApprovalNumber extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      approvalNumber: '',
    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }

  render() {
    const { handleChange } = this;
    const { approvalNumber } = this.state;
    return (
      <TextValidator
        name="approvalNumber"
        id="approvalNumber"
        required
        onChange={handleChange}
        value={approvalNumber}
        label="Approval Number"
        helperText="เลขที่อนุมัติหลายเที่ยวเรือ"
        placeholder="YY0000"
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[0-9]*$',
          'maxStringLength:6',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'กรอกได้เฉพาะตัวเลข',
          'ต้องกรอกไม่เกิน 6 หลัก',
        ]}
        margin="dense"
        variant="outlined"
      />
    );
  }
}

export default ApprovalNumber;
