import React from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';

const SubBrokerTaxNumber = React.memo(props => {
  const { handleChange, subBrokerTaxNumber } = props;
  return (
    <TextValidator
      id="subBrokerTaxNumber"
      name="subBrokerTaxNumber"
      label="Sub Broker Tax Number"
      value={subBrokerTaxNumber}
      onChange={handleChange}
      fullWidth
      helperText="เลขประจำตัวผู้เสียภาษีอากรของบริษัทผู้ขาย"
      validators={['maxStringLength: 17', 'matchRegexp:^[A-Za-z0-9]*$']}
      errorMessages={[
        'กรอกได้สูงสุด 17 ตัวอักษร',
        'กรอกได้เฉพาะตัวเลขหรือตัวอักษรเท่านั้น',
      ]}
      margin="dense"
      variant="outlined"
    />
  );
});

export default SubBrokerTaxNumber;
