import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

class ApprovalPort extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      approvalPort: '',
    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }

  render() {
    const { handleChange } = this;
    const { approvalPort } = this.state;
    return (
      <TextValidator
        name="approvalPort"
        id="approvalPort"
        required
        onChange={handleChange}
        value={approvalPort}
        label="Approval Port"
        helperText="รหัสสถานที่อนุมัติใบขนสินค้าหลายเที่ยวเรือ"
        placeholder="0000"
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[0-9]*$',
          'maxStringLength:4',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'กรอกได้เฉพาะตัวเลข',
          'ต้องกรอกไม่เกิน 4 หลัก',
        ]}
        margin="dense"
        variant="outlined"
      />
    );
  }
}

export default ApprovalPort;
