import React, { Fragment } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const AddressField = React.memo(props => {
  const { streetAndNumber, handleChange } = props;
  return (
    <Fragment>
      <TextValidator
        name="streetAndNumber"
        id="streetAndNumber"
        required
        onChange={handleChange}
        value={streetAndNumber}
        label="ที่อยู่บริษัท"
        placeholder="เลขที่ 5 ถนนเจริญกรุง"
        fullWidth
        validators={['required', 'maxStringLength:70']}
        errorMessages={['จำเป็นต้องกรอก', 'ต้องกรอกไม่เกิน 70 ตัวอักษร']}
        variant="outlined"
        margin="dense"
      />
    </Fragment>
  );
});

export default AddressField;
