import React, { PureComponent, Fragment } from 'react';
import portCodes from '../../../../../jsonData/portCodes.json';
import { TextValidator } from 'react-material-ui-form-validator';
import { ListItem, ListItemText } from '@material-ui/core';
import SearchSelectWrapper from '../../../../shared/searchSelectorComponents/SearchSelectWrapper.js';

class PortSearch extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      portCodesData: [],
      searchInput: '',
      selected: false,
      validators: [],
      errorMessages: [],
      anchorEl: null,
    };
  }

  componentDidMount() {
    const { required } = this.props;
    this.interval = null;
    this.setState({
      portCodesData: portCodes,
      validators: required ? ['required'] : [],
      errorMessages: required ? ['จำเป็นต้องกรอก'] : [],
    });
  }

  componentWillUnmount() {
    this.setState({
      portCodesData: [],
    });
    clearTimeout(this.interval);
  }

  handleBlur = e => {
    e.preventDefault();
    this.interval = setTimeout(() => {
      this.setState(state => ({
        searchInput: state.selected ? state.searchInput : '',
      }));
    }, 200);
  };

  handleSeachInput = e => {
    const { currentTarget, target } = e;
    e.preventDefault();
    this.setState({
      searchInput: target.value,
      selected: false,
      anchorEl: currentTarget,
      anchorElWidth: currentTarget.offsetWidth,
    });
  };

  handleSelect = e => {
    const { required } = this.props;
    e.preventDefault();
    this.setState({
      selected: true,
      searchInput: e.currentTarget.innerText.trim().slice(0, 4),
      validators: required ? ['required', 'maxStringLength: 4'] : [],
      errorMessages: ['จำเป็นต้องกรอก', 'กรอกได้ 4 ตัวอักษร'],
    });
  };

  render() {
    let firstFilteredPortCodes = [];
    const { handleSeachInput, handleSelect, handleBlur } = this;
    const { id, label, helperText, isRelease, required } = this.props;
    const {
      searchInput,
      validators,
      errorMessages,
      selected,
      portCodesData,
      anchorEl,
      anchorElWidth,
    } = this.state;

    const lowerCasedInput = searchInput.toLowerCase();

    isRelease
      ? (firstFilteredPortCodes = portCodesData.filter(
          port => port.releasePort === 'Y'
        ))
      : (firstFilteredPortCodes = portCodesData.filter(
          port => port.dischargePort === 'Y'
        ));

    const filteredPortCodes = firstFilteredPortCodes.filter(
      port =>
        port.portNumber.toLowerCase().includes(lowerCasedInput) ||
        port.portName.toLowerCase().includes(lowerCasedInput)
    );

    return (
      <Fragment>
        <TextValidator
          id={id}
          name={id}
          fullWidth
          required={required}
          label={label}
          helperText={helperText}
          placeholder="ค้นหาด้วยรหัสสถานที่ / ชื่อสถานที่"
          type="search"
          onBlur={handleBlur}
          value={searchInput}
          validators={validators}
          errorMessages={errorMessages}
          onChange={handleSeachInput}
          variant="outlined"
          margin="dense"
        />
        {selected || searchInput === '' ? (
          ''
        ) : (
          <SearchSelectWrapper
            anchorEl={anchorEl}
            anchorElWidth={anchorElWidth}
          >
            {filteredPortCodes.slice(0, 20).map(port => (
              <ListItem
                button
                key={port.portNumber}
                onClick={handleSelect}
                value={port.portNumber}
              >
                <ListItemText
                  primary={`${port.portNumber}`}
                  secondary={`${port.portName}`}
                />
              </ListItem>
            ))}
          </SearchSelectWrapper>
        )}
      </Fragment>
    );
  }
}

export default PortSearch;
