import React, { PureComponent, Fragment } from 'react';
import {
  FormControlLabel,
  FormControl,
  FormLabel,
  Radio,
  RadioGroup,
  Collapse,
} from '@material-ui/core';

class RadioControl extends PureComponent {
  state = {
    value: '',
    checked: false,
  };

  handleChange = e => {
    const checkedValue = e.target.value;
    this.setState({ value: checkedValue });
    checkedValue === 'Y'
      ? this.setState({
          checked: true,
        })
      : this.setState({
          checked: false,
        });
  };

  render() {
    const { formLabel, radioName, yesLabel, noLabel } = this.props;
    const { value, checked } = this.state;
    const { handleChange } = this;
    return (
      <Fragment>
        <FormControl
          fullWidth
          component="fieldset"
          style={{ margin: 8, marginTop: 25 }}
        >
          <FormLabel component="legend">{formLabel}</FormLabel>
          <RadioGroup
            name={radioName}
            value={value}
            onChange={handleChange}
          >
            <FormControlLabel
              value="Y"
              control={<Radio />}
              label={yesLabel}
            />
            <FormControlLabel
              value="N"
              control={<Radio />}
              label={noLabel}
            />
          </RadioGroup>
        </FormControl>
        <Collapse in={checked}>
          <Fragment>{this.props.children}</Fragment>
        </Collapse>
      </Fragment>
    );
  }
}

export default RadioControl;
