import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { InputAdornment } from '@material-ui/core';
import AdditionalInfoDialog from '../../../shared/AdditionalInfoDialog';
import vesselData from './infoBody/vesselData';
import TableInfoBody from './infoBody/tableInfoBody';

const VesselName = React.memo(props => {
  const { handleChange, vesselName, placeholder } = props;
  return (
    <TextValidator
      name="vesselName"
      id="vesselName"
      required
      onChange={handleChange}
      value={vesselName}
      label="Vessel Name"
      helperText="ชื่อยานพาหนะ"
      placeholder={placeholder}
      InputProps={{
        endAdornment: (
          <InputAdornment position="end">
            <AdditionalInfoDialog
              maxWidth="sm"
              titleTH="ชื่อยานพาหนะ"
              titleEN="Vessel Name"
              format="an..35"
            >
              <TableInfoBody
                firstHeader="นำเข้าทาง"
                thirdHeader="กรอก"
                data={vesselData}
              />
            </AdditionalInfoDialog>
          </InputAdornment>
        ),
      }}
      fullWidth
      validators={['required', 'maxStringLength:35']}
      errorMessages={['จำเป็นต้องกรอก', 'ต้องกรอกไม่เกิน 35 หลัก']}
      variant="outlined"
      margin="dense"
    />
  );
});

export default VesselName;
