import React, { PureComponent, Fragment } from 'react';
import {
  RadioGroup,
  FormControlLabel,
  Radio,
  FormLabel,
} from '@material-ui/core';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';

class ShippingMarks extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      radioValue: '',
      readonly: true,
      multiline: false,
      validators: ['required'],
      errorMessages: ['จำเป็นต้องกรอก'],
      shippingMarks: '',
      radioSelected: false,
      placeholder: 'กรุณาเลือกจากด้านบนก่อน',
    };
  }

  handleChange = name => e => {
    this.setState({
      [name]: e.target.value,
    });
  };

  setReadonlyFieldCase = message => {
    this.setState({
      shippingMarks: message,
      readonly: true,
      validators: ['required'],
      errorMessages: ['จำเป็นต้องกรอก'],
      multiline: false,
      placeholder: '',
    });
  };

  setEnabledFieldCase = () => {
    this.setState({
      shippingMarks: '',
      readonly: false,
      validators: ['required', 'maxStringLength: 512'],
      errorMessages: ['จำเป็นต้องกรอก', 'กรอกได้สูงสุด 512 ตัวอักษร'],
      multiline: true,
      placeholder: 'ระบุรายละเอียด',
    });
  };

  handleRadioSelect = e => {
    const { setReadonlyFieldCase, setEnabledFieldCase } = this;
    const radioValue = e.target.value;
    this.setState({ radioValue });
    switch (radioValue) {
      case '0':
        setReadonlyFieldCase('NO SHIPPING MARK');
        break;
      case '1':
      case '3':
        setReadonlyFieldCase('PICTURE');
        break;
      case '2':
        setEnabledFieldCase();
        break;
      default:
        break;
    }
  };

  render() {
    const { handleChange, handleRadioSelect } = this;
    const {
      radioValue,
      shippingMarks,
      multiline,
      readonly,
      validators,
      errorMessages,
      placeholder,
    } = this.state;
    return (
      <Fragment>
        <FormLabel component="legend">
          เครื่องหมายและเลขหมายหีบห่อ
        </FormLabel>
        <RadioGroup
          aria-label="Shipping Marks Radio"
          name="shippingMarkRadio"
          value={radioValue}
          onChange={handleRadioSelect}
        >
          <FormControlLabel
            value="0"
            control={<Radio />}
            label="ไม่มี Shipping Mark"
          />
          <FormControlLabel
            value="1"
            control={<Radio />}
            label="เป็นรูปภาพ"
          />
          <FormControlLabel
            value="2"
            control={<Radio />}
            label="เป็นข้อความ"
          />
          <FormControlLabel
            value="3"
            control={<Radio />}
            label="เป็นภาษาต่างประเทศที่กรอกไม่ได้"
          />
        </RadioGroup>
        <TextValidator
          required
          name="shippingMarks"
          id="shippingMarks"
          onChange={handleChange('shippingMarks')}
          value={shippingMarks}
          label="Shipping Marks"
          helperText="เครื่องหมายและเลขหมายหีบห่อ"
          placeholder={placeholder}
          fullWidth
          multiline={multiline}
          rows="8"
          inputProps={{ readOnly: readonly }}
          validators={validators}
          errorMessages={errorMessages}
          margin="dense"
          variant="outlined"
        />
      </Fragment>
    );
  }
}

export default ShippingMarks;
