import React, { PureComponent, Fragment } from 'react';
import ShippingMarks from './fields/idc3Fields/ShippingMarks';
import { ValidatorForm } from 'react-material-ui-form-validator';
import FieldHeader from '../shared/FieldHeader';
import TotalPackageAmount from './fields/idc3Fields/TotalPackageAmount';
import TotalPackageUnitCode from './fields/idc3Fields/TotalPackageUnitCode';
import TotalNetWeight from './fields/idc3Fields/TotalNetWeight';
import NetWeightUnitCode from './fields/idc3Fields/NetWeightUnitCode';
import TotalGrossWeight from './fields/idc3Fields/TotalGrossWeight';
import { addCustomValidationRules } from '../shared/customValidations';
import TotalGrossWeightUnitCode from './fields/idc3Fields/TotalGrossWeightUnitCode';
import CurrencyCode from './fields/idc3Fields/CurrencyCode';
import ExchangeRate from './fields/idc3Fields/ExchangeRate';
import CIFValueForeign from './fields/idc3Fields/CIFValueForeign';
import CIFValueBaht from './fields/idc3Fields/CIFValueBaht';

class ImportDeclarationControl3 extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      totalPackageAmount: '',
      totalPackageUnitCode: '',
      totalNetWeight: '',
      netWeightUnitCode: '',
      totalGrossWeight: '',
      totalGrossWeightUnitCode: '',
      exchangeRate: '',
      cifValueForeign: '',
    };
    this.CIFValueForeignRef = React.createRef();
    this.ExchangeRateRef = React.createRef();
  }

  componentWillMount() {
    addCustomValidationRules(this);
  }

  saveToLocalStorage = data => {
    for (const pair of data.entries()) {
      localStorage.setItem(pair[0], pair[1]);
    }
  };

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    const data = new FormData(e.target);
    this.saveToLocalStorage(data);
    this.props.handlenext();
  };

  render() {
    const { handleChange, handleSubmit } = this;
    const {
      totalPackageAmount,
      totalPackageUnitCode,
      totalNetWeight,
      netWeightUnitCode,
      totalGrossWeight,
      totalGrossWeightUnitCode,
      exchangeRate,
      cifValueForeign,
    } = this.state;
    return (
      <Fragment>
        <ValidatorForm
          onSubmit={handleSubmit}
          noValidate
          autoComplete="off"
        >
          <FieldHeader>รายละเอียดการขนส่ง (ต่อ) </FieldHeader>
          <ShippingMarks />
          <TotalPackageAmount
            handleChange={handleChange}
            totalPackageAmount={totalPackageAmount}
          />
          <TotalPackageUnitCode
            handleChange={handleChange}
            totalPackageUnitCode={totalPackageUnitCode}
          />
          <TotalNetWeight
            handleChange={handleChange}
            totalNetWeight={totalNetWeight}
          />
          <NetWeightUnitCode
            handleChange={handleChange}
            netWeightUnitCode={netWeightUnitCode}
          />
          <TotalGrossWeight
            handleChange={handleChange}
            totalGrossWeight={totalGrossWeight}
          />
          <TotalGrossWeightUnitCode
            handleChange={handleChange}
            totalGrossWeightUnitCode={totalGrossWeightUnitCode}
          />
          <FieldHeader>ข้อมูลด้านการเงิน</FieldHeader>
          <CurrencyCode />
          <ExchangeRate
            ref={this.ExchangeRateRef}
            handleChange={handleChange}
            exchangeRate={exchangeRate}
          />
          <CIFValueForeign
            ref={this.CIFValueForeignRef}
            handleChange={handleChange}
            cifValueForeign={cifValueForeign}
          />
          <CIFValueBaht
            CIFForeignRef={this.CIFValueForeignRef.current}
            exchangeRateRef={this.ExchangeRateRef.current}
          />
          {this.props.children}
        </ValidatorForm>
      </Fragment>
    );
  }
}

export default ImportDeclarationControl3;
