const validateFloat = (overallMax, maxDecimals, value) => {
  if (value.includes('.')) {
    const valueArray = value.split('');
    const intPart = value.split('.')[0];
    const floatPart = value.split('.')[1];
    // Checks if there are more than one dots
    if (valueArray.filter(digit => digit === '.').length > 1) {
      return false;
    } else if (
      intPart.length > overallMax ||
      floatPart.length > maxDecimals ||
      floatPart.length === 0
    ) {
      return false;
    }
    // checks if the combined value has more than max digits
    else if (intPart.length + floatPart.length > overallMax) {
      return false;
    }
    return true;
  } else if (value.length > overallMax) {
    return false;
  } else {
    return true;
  }
};

export default validateFloat;
