import React from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';

const TaxCardBankAccountNumber = React.memo(props => {
  const { handleChange, taxCardBankAccountNumber } = props;
  return (
    <TextValidator
      required
      name="taxCardBankAccountNumber"
      id="taxCardBankAccountNumber"
      onChange={handleChange}
      value={taxCardBankAccountNumber}
      label="Tax Card Bank Account Number"
      helperText="เลขบัญชีเดบิตบัตรภาษีของผู้นำเข้า"
      placeholder="0000000000"
      fullWidth
      validators={[
        'required',
        'matchRegexp:^[A-Za-z0-9]*$',
        'maxStringLength:35',
      ]}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'กรอกได้เฉพาะตัวอักษรภาษาอังกฤษและตัวเลข',
        'กรอกได้สูงสุด 35 หลัก',
      ]}
      margin="dense"
      variant="outlined"
    />
  );
});

export default TaxCardBankAccountNumber;
