import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const CustomsClearanceID = React.memo(props => {
  const { handleChange, customsClearanceId } = props;
  return (
    <TextValidator
      name="customsClearanceId"
      id="customsClearanceId"
      required
      onChange={handleChange}
      value={customsClearanceId}
      label="Customs Clearance ID"
      helperText="เลขบัตรประชาชนผู้ปฏิบัติหน้าที่เกี่ยวกับการผ่านพิธีการศุลกากร"
      fullWidth
      validators={[
        'required',
        'matchRegexp:^[A-Za-z0-9]*$',
        'maxStringLength:17',
      ]}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'กรอกเฉพาะตัวอักษรหรือตัวเลข',
        'ต้องกรอกไม่เกิน 17 หลัก',
      ]}
      variant="outlined"
      margin="dense"
    />
  );
});

export default CustomsClearanceID;
