import React from 'react';
import { Popper, Paper } from '@material-ui/core';

const SearchSelectWrapper = React.memo(props => {
  return (
    <Popper
      modifiers={{ flip: { enabled: false } }}
      open
      anchorEl={props.anchorEl}
      style={{ zIndex: 2 }}
    >
      <Paper
        style={{
          width: `${props.anchorElWidth}px`,
          overflowY: 'auto',
          maxHeight: '290px',
          minHeight: 0,
        }}
      >
        {props.children}
      </Paper>
    </Popper>
  );
});

export default SearchSelectWrapper;
