import React, { PureComponent, Fragment } from 'react';
import {
  withStyles,
  CssBaseline,
  Paper,
  Stepper,
  Step,
  StepLabel,
  Button,
  Typography,
} from '@material-ui/core';
import ImportDeclarationControl1 from './forms/idc1';
import ImportDeclarationControl2 from './forms/idc2';
import ImportDeclarationControl3 from './forms/idc3';
import ImportDeclarationControl4 from './forms/idc4';
import ImportDeclarationControl5 from './forms/idc5';

const styles = theme => ({
  layout: {
    width: 'auto',
    [theme.breakpoints.up(650 + theme.spacing.unit * 2 * 2)]: {
      width: 650,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginBottom: theme.spacing.unit * 3,
    padding: theme.spacing.unit * 3,
    [theme.breakpoints.up(650 + theme.spacing.unit * 3 * 2)]: {
      marginTop: theme.spacing.unit * 6,
      marginBottom: theme.spacing.unit * 6,
      padding: theme.spacing.unit * 3,
    },
  },
  stepper: {
    padding: `${theme.spacing.unit * 3}px 0 ${theme.spacing.unit * 5}px`,
  },
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing.unit * 3,
    marginLeft: theme.spacing.unit,
  },
});

const steps = [
  'ข้อมูลบริษัท',
  'การขนส่ง',
  'การขนส่ง (ต่อ)',
  'การชำระเงิน',
  'อื่นๆ',
];

class FormPage extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
    };
    this.getStepContent = this.getStepContent.bind(this);
  }

  handleNext = () => {
    this.setState(state => ({
      activeStep: state.activeStep + 1,
    }));
  };

  handleBack = () => {
    this.setState(state => ({
      activeStep: state.activeStep - 1,
    }));
  };

  handleReset = () => {
    this.setState({
      activeStep: 0,
    });
  };

  getStepContent(step) {
    switch (step) {
      case 0:
        return (
          <ImportDeclarationControl1 handlenext={this.handleNext}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              className={this.props.classes.button}
            >
              NEXT
            </Button>
          </ImportDeclarationControl1>
        );
      case 1:
        return (
          <ImportDeclarationControl2 handlenext={this.handleNext}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              className={this.props.classes.button}
            >
              NEXT
            </Button>
          </ImportDeclarationControl2>
        );
      case 2:
        return (
          <ImportDeclarationControl3 handlenext={this.handleNext}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              className={this.props.classes.button}
            >
              NEXT
            </Button>
          </ImportDeclarationControl3>
        );
      case 3:
        return (
          <ImportDeclarationControl4 handlenext={this.handleNext}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              className={this.props.classes.button}
            >
              NEXT
            </Button>
          </ImportDeclarationControl4>
        );
      case 4:
        return (
          <ImportDeclarationControl5 handlenext={this.handleNext}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              className={this.props.classes.button}
            >
              NEXT
            </Button>
          </ImportDeclarationControl5>
        );
      default:
        throw new Error('Unknown step');
    }
  }

  render() {
    const { classes } = this.props;
    const { activeStep } = this.state;

    return (
      <Fragment>
        <CssBaseline />
        <main className={classes.layout}>
          <Paper className={classes.paper}>
            <Typography
              color="textSecondary"
              variant="h5"
              align="center"
              gutterBottom
            >
              ใบขนสินค้าขาเข้า
            </Typography>
            <Typography align="center" variant="h5" gutterBottom>
              Import Declaration Control (1)
            </Typography>
            <Stepper activeStep={activeStep} className={classes.stepper}>
              {steps.map(label => (
                <Step key={label}>
                  <StepLabel>{label}</StepLabel>
                </Step>
              ))}
            </Stepper>
            <Fragment>
              {activeStep === steps.length ? (
                <Fragment>
                  <Typography variant="h5" gutterBottom>
                    Thank you for your order.
                  </Typography>
                  <Typography variant="subtitle1">
                    Your order number is #2001539. We have emailed your
                    order confirmation, and will send you an update when
                    your order has shipped.
                  </Typography>
                </Fragment>
              ) : (
                <Fragment>
                  {this.getStepContent(activeStep)}
                  <div className={classes.buttons}>
                    {activeStep !== 0 && (
                      <Button
                        onClick={this.handleBack}
                        className={classes.button}
                      >
                        Back
                      </Button>
                    )}
                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      onClick={this.handleNext}
                      className={classes.button}
                    >
                      {activeStep === steps.length - 1 ? 'Submit' : 'Next'}
                    </Button>
                  </div>
                </Fragment>
              )}
            </Fragment>
          </Paper>
        </main>
      </Fragment>
    );
  }
}

export default withStyles(styles)(FormPage);
