import React, { PureComponent } from 'react';
import { MenuItem, Popper, Paper } from '@material-ui/core';

class TambonSelect extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      anchorElWidth: '',
      open: false,
    };
  }

  componentDidMount() {
    this.setState({
      anchorElWidth: this.props.anchorEl.offsetWidth,
    });
  }

  render() {
    const {
      selected,
      searchInput,
      handleSelect,
      filteredTambons,
      anchorEl,
      anchorElWidth,
    } = this.props;

    return selected || searchInput === '' ? (
      ''
    ) : (
      <Popper open anchorEl={anchorEl} style={{ zIndex: 2 }}>
        <Paper style={{ width: `${anchorElWidth}px` }}>
          {filteredTambons.slice(0, 5).map(tambon => (
            <MenuItem
              key={tambon.pid}
              onClick={handleSelect}
              value={tambon.name}
            >
              {tambon.name}
            </MenuItem>
          ))}
        </Paper>
      </Popper>
    );
  }
}

export default TambonSelect;
