import React from 'react';
import SelectValidator from 'react-material-ui-form-validator/lib/SelectValidator';
import { MenuItem } from '@material-ui/core';

const GuaranteeType = React.memo(props => {
  const { handleChange, guaranteeType, guaranteeTypeData } = props;

  return guaranteeTypeData.length === 0 ? (
    ''
  ) : (
    <SelectValidator
      id="guaranteeType"
      required
      name="guaranteeType"
      onChange={handleChange}
      value={guaranteeType}
      select
      label="Guarantee Type"
      helperText="ประเภทการวางประกัน"
      fullWidth
      validators={['required']}
      errorMessages={['จำเป็นต้องเลือก']}
      margin="dense"
      variant="outlined"
    >
      {guaranteeTypeData.map(option => (
        <MenuItem key={option.value} value={option.value}>
          {option.label}
        </MenuItem>
      ))}
    </SelectValidator>
  );
});

export default GuaranteeType;
