import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';

const ManagerName = React.memo(props => {
  const { handleChange, managerName } = props;
  return (
    <TextValidator
      name="managerName"
      id="managerName"
      required
      onChange={handleChange}
      value={managerName}
      label="Manager Name"
      helperText="ชื่อและนามสกุลผู้จัดการ | ผู้รับมอบอำนาจของบริษัทหรือ Broker"
      fullWidth
      validators={['required', 'maxStringLength:35']}
      errorMessages={['จำเป็นต้องกรอก', 'ต้องกรอกไม่เกิน 35 หลัก']}
      variant="outlined"
      margin="dense"
    />
  );
});

export default ManagerName;
