import React from 'react';
import {
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Typography,
} from '@material-ui/core';

const TableInfoBody = React.memo(props => {
  const { data, firstHeader, thirdHeader } = props;
  return (
    <div>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>
              <Typography color="textSecondary">{firstHeader}</Typography>
            </TableCell>
            <TableCell>
              <Typography color="textSecondary">{thirdHeader}</Typography>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map((datum, i) => {
            return (
              <TableRow hover key={i}>
                <TableCell>
                  <Typography>{datum.mode}</Typography>
                </TableCell>
                <TableCell>
                  <Typography>{datum.enter}</Typography>
                </TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </div>
  );
});

export default TableInfoBody;
