import React, { PureComponent, Fragment } from 'react';
import CustomsBankCode from './CustomsBankCode';
import BankCode from './BankCode';
import BankBranchCode from './BankBranchCode';
import BankAccountNumber from './BankAccountNumber';
import TotalPaymentAmount from './TotalPaymentAmount';
import RadioControl from './RadioControl';
import TaxCardBankCode from './TaxCardBankCode';
import TaxCardBankBranchCode from './TaxCardBankBranchCode';
import TaxCardBankAccountNumber from './TaxCardBankAccountNumber';
import TotalTaxCardPaymentAmount from './TotalTaxCardPaymentAmount';
import FieldHeader from '../../../shared/FieldHeader';

class BankFields extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      customsBankCode: '',
      bankCode: '',
      bankBranchCode: '',
      bankAccountNumber: '',
      totalPaymentAmount: '',
      taxCardBankCode: '',
      taxCardBankBranchCode: '',
      taxCardBankAccountNumber: '',
      totalTaxCardPaymentAmount: '',
    };
    this.shouldBeVisible = this.shouldBeVisible.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    this.setState({
      [e.target.name]: e.target.value,
    });
  }

  shouldBeVisible() {
    if (
      this.props.paymentMethod === 'H' ||
      (this.props.guaranteeMethod === 'H' &&
        this.props.guaranteeType === 'C')
    ) {
      return true;
    } else {
      return false;
    }
  }

  render() {
    const {
      customsBankCode,
      bankCode,
      bankBranchCode,
      bankAccountNumber,
      totalPaymentAmount,
      taxCardBankCode,
      taxCardBankBranchCode,
      taxCardBankAccountNumber,
      totalTaxCardPaymentAmount,
    } = this.state;

    const { handleChange } = this;

    return this.shouldBeVisible() ? (
      <Fragment>
        <RadioControl
          formLabel="ต้องการชําระค่าภาษีด้วยเดบิตบัตรภาษี"
          radioName="payWithTaxCard"
          yesLabel="ต้องการ"
          noLabel="ไม่ต้องการ"
        >
          <TaxCardBankCode
            handleChange={handleChange}
            taxCardBankCode={taxCardBankCode}
          />
          <TaxCardBankBranchCode
            handleChange={handleChange}
            taxCardBankBranchCode={taxCardBankBranchCode}
          />
          <TaxCardBankAccountNumber
            handleChange={handleChange}
            taxCardBankAccountNumber={taxCardBankAccountNumber}
          />
          <TotalTaxCardPaymentAmount
            handleChange={handleChange}
            totalTaxCardPaymentAmount={totalTaxCardPaymentAmount}
          />
        </RadioControl>
        <FieldHeader>ธนาคารที่ใช้จ่ายภาษี</FieldHeader>
        <CustomsBankCode
          handleChange={handleChange}
          customsBankCode={customsBankCode}
        />
        <BankCode handleChange={handleChange} bankCode={bankCode} />
        <BankBranchCode
          handleChange={handleChange}
          bankBranchCode={bankBranchCode}
        />
        <BankAccountNumber
          handleChange={handleChange}
          bankAccountNumber={bankAccountNumber}
        />
        <TotalPaymentAmount
          handleChange={handleChange}
          totalPaymentAmount={totalPaymentAmount}
        />
      </Fragment>
    ) : null;
  }
}

export default BankFields;
